      if (self.selectUser) {
        if (self.$tools.isNull(data)) {
          self.showSelectUser = true;
          self.showBasic = false;
        } else {
          self.selectedUser = data;
          self.showSelectUser = false;
          self.showBasic = true;
        }
      }
    },
    close() {
      this.$emit("close");
      this.modalClose();
    },
    sendInvite() {
      this.$emit("sendInvite");
      this.modalClose();
    },
    update() {
      this.$emit("update");
      this.modalClose();
    },
    modalClose() {
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },
    init() {
      let self = this;
      if (!self.$tools.isNull(self.name)) {
        self.fullName = self.name;
      }
      if (!self.$tools.isNull(self.contactData)) {
        self.selectedUser = self.contactData;
      }
      if (!self.$tools.isNull(self.data)) {
        self.fullName = self.data.name;
        self.phone = self.data.phone;
        self.email = self.data.email;
      }
      setTimeout(() => {
        self.show = true;
      }, 200);
    },
    selectDuration(item) {
      let self = this;
      self.durations.some(time => {
        time.selected = false;
      });
      item.selected = true;
      self.newType.duration = item.length;
    }
